import React, {ChangeEvent, useState, useEffect, useCallback,useRef} from 'react';
import {useTranslation} from 'react-i18next';

import {Button, Form, Input, Select, ConfigProvider, Radio, Upload, App, FormInstance,Modal , Spin , InputNumber} from 'antd';
import type {RadioChangeEvent} from 'antd'
import useWindowSize from '../../utils/useWindowSize'
import type {RcFile, UploadFile, UploadProps} from 'antd/es/upload/interface'
import {UploadOutlined} from '@ant-design/icons';
import {useStore} from '../../utils/store'
import {formRequest, request} from '../../api/request'
import tabArr from '../../utils/currency'
import { debounce } from "lodash"


const onFinish = (values: any) => {
    console.log('Success:', values);
};

const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo);
};


function AddChart() {

    const {t, i18n} = useTranslation();
    const { width, height } = useWindowSize()
    const [isOffset,setIsOffset] = useState(8)
    
    const [valueTrend, setValueTrend] = useState(`${t('bPrice')}`);

    const [state, dispatch] = useStore();
    const formRef = React.useRef<FormInstance>(null);

    const {message} = App.useApp()

    const onRadioChange = (e: RadioChangeEvent) => {
        const v = e.target.value === 'long' ? `${t('bPrice')}` : `${t('sPrice')}`
        setValueTrend(v);
    };
    
    //防抖
    const [listLoading,setListLoading] = useState(false)
    //选择是否售卖
    const [valueSell, setValueSell] = useState(0)


    //上传图片
    const [fileList, setFileList] = useState<UploadFile[]>([]);
    const [uploading, setUploading] = useState(false);

    const props: UploadProps = {
        onRemove: (file) => {
            const index = fileList.indexOf(file);
            const newFileList = fileList.slice();
            newFileList.splice(index, 1);
            setFileList(newFileList);
        },
        beforeUpload: (file) => {
            const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;
            //控制上传图片格式
            if (!isJpgOrPng || !isLt2M) {
                if (!isJpgOrPng) {
                    message.error(`${t('YCImg')}`);
                }
                if (!isLt2M) {
                    message.error(`${t('YCImgSize')}`);
                }
                return Upload.LIST_IGNORE
            } else {
                setFileList([...fileList, file]);
            }
            return false
        },
        onChange: (info) => {
            console.log(info.fileList);
        },
        fileList,
    };


    const checkPic = (rule: any, value: []) => {
        return new Promise((resolve, reject) => {
            if (fileList.length < 1) {
                reject(new Error(`${t('PLUpload')}`))
            } else {
                resolve(fileList)
            }
        })
    }

    const onFinish = (values: any) => {
        if (state.userId == null) {
            message.error(`${t('ErrorUser')}`)
        } else {
            const formData = new FormData();
            fileList.forEach((file) => {
                formData.append('image', file as RcFile);
            });
            formData.append("title", values.title)
            formData.append("token", values.token)
            formData.append("timeperiod", values.timeperiod)
            formData.append("trend", values.trend)
            formData.append("price", values.price)
            formData.append("stoplossprice", values.slprice)
            formData.append("takeprofit", values.tprofit)
            formData.append("expiration_date", "0")
            if(values.is_for_sale==1){
                formData.append("is_for_sale", values.is_for_sale)
                formData.append("post_price", values.post_price)
                formData.append("content", values.content)
                formData.append("post_quantity", values.post_quantity)
            }
           debouncedSave(formData)
        }
    };

    
    const debouncedSave = useCallback(
        debounce((val:any) => getSubmit(val), 700),
        [], // 仅在初次渲染调用
    );
    

    const onFinishFailed = (errorInfo: any) => {
        console.log('Failed:', errorInfo);
    };
    
    const onRadioSell = (e: RadioChangeEvent) =>{
        setValueSell(e.target.value)
    }
    
    const SellHtml = () =>{

        if(valueSell == 1){
            return (
                        <><Form.Item
                            label={t("invoicelogic")}
                            name="content"
                            rules={[{required: true, message: `${t('Required')}`}]}
                            >
                            <Input.TextArea/>
                        </Form.Item>
                            <Form.Item
                                label={t("saleprice")}
                                name="post_price"
                                rules={[{required: true, message: `${t('Required')}`}]}
                                >
                                <Input/>
                            </Form.Item>
                    <Form.Item
                        label={t("salesquantity")}
                        name="post_quantity"
                        rules={[{required: true, message: `${t('Required')}`}]}
                        >
                        <InputNumber min={1} max={999}/>
                    </Form.Item></>
            )
        }else{
            return (<></>) 
        }
       
    }

    //提交表单
    const getSubmit = async (value: any) => {
        try {
            await formRequest.post('/sellpost/post_for_sell', value).then(res => {
                if (res.data.code != 200) {
                    message.error(res.data.message)
                } else {
                    setFileList([]);
                    formRef.current?.resetFields();
                    message.success(`${t('SubmitTrue')}`)
                }
            })
        } catch (error) {
            console.log(error)
        }

    };
    
    let ignore = false;

    useEffect(() => {
        async function startFetching() {
            if (!ignore) {
                if(width>767){
                    setIsOffset(8)
                }else{
                    setIsOffset(0)
                }
            }
        }

        startFetching();

        return () => {
            ignore = true;
        };

        }, [width]);
    return (
        <div className="home-content addchart-content">
                <Form
                    ref={formRef}
                    name="addchart"
                    labelCol={{span: 8}}
                    wrapperCol={{span: 16}}
                    style={{maxWidth: 600}}
                    initialValues={{trend: "long",is_for_sale:0}}
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                    autoComplete="off"
                >
                    <Form.Item
                        label={t('Title')}
                        name="title"
                        rules={[{required: true, message: `${t('Required')}`}]}
                    >
                        <Input/>
                    </Form.Item>
                    <Form.Item label={t('token')} name="token" rules={[{required: true, message: `${t('Required')}`}]}>
                        <Select
                            showSearch
                            placeholder="Select a person"
                            optionFilterProp="children"
                            filterOption={(input, option) =>
      (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
    }
                            options = {tabArr}
                        />
                    </Form.Item>
                    <Form.Item
                        label={t('timePeriod')}
                        name="timeperiod"
                        rules={[{required: true, message: `${t('Required')}`}]}
                    >
                        <Select>
                            <Select.Option value="1D">1D</Select.Option>
                            <Select.Option value="8h">8h</Select.Option>
                            <Select.Option value="4h">4h</Select.Option>
                            <Select.Option value="2h">2h</Select.Option>
                            <Select.Option value="1h">1h</Select.Option>
                            <Select.Option value="30m">30m</Select.Option>
                            <Select.Option value="15m">15m</Select.Option>
                            <Select.Option value="5m">5m</Select.Option>
                        </Select>
                    </Form.Item>
                    <Form.Item label={t('trend')} name="trend">
                        <Radio.Group onChange={onRadioChange} value={valueTrend}>
                            <Radio value={'long'}> {t('long')} </Radio>
                            <Radio value={'short'}> {t('short')} </Radio>
                        </Radio.Group>
                    </Form.Item>
                    <Form.Item
                        label={valueTrend}
                        name="price"
                        rules={[{required: true, message: `${t('Required')}`}]}
                    >
                        <Input/>
                    </Form.Item>
                    <Form.Item
                        label={t('SLPrice')}
                        name="slprice"
                        rules={[{required: true, message: `${t('Required')}`}]}
                    >
                        <Input/>
                    </Form.Item>
                    <Form.Item
                        label={t('TProfit1')}
                        name="tprofit"
                        rules={[{required: true, message: `${t('Required')}`}]}
                    >
                        <Input/>
                    </Form.Item>
                    <Form.Item required label={t('Upload')} name="fileList" rules={[{validator: checkPic}]}>
                        <Upload {...props} maxCount={1}>
                            {
                                fileList && fileList.length >= 1 ? null : (
                                    <Button icon={<UploadOutlined/>}>Select File</Button>
                                )
                            }
                        </Upload>
                    </Form.Item>
                    <Form.Item label={t("Issale")} name="is_for_sale">
                        <Radio.Group onChange={onRadioSell} value={valueSell}>
                            <Radio value={0}> {t("No")} </Radio>
                            <Radio value={1}> {t("Yes")}</Radio>
                        </Radio.Group>
                    </Form.Item>
                    <SellHtml/>
                    <Form.Item wrapperCol={{offset: isOffset, span: 16}}>
                        <Button type="primary" htmlType="submit">
                            {t('Submit')}
                        </Button>
                        <Button htmlType="reset">{t('reset')}</Button>
                    </Form.Item>
                </Form>
        </div>
    )
}

export default AddChart;